/* General Styles */
body {
    margin: 0;
    padding: 0;
    font-family: "Inter", sans-serif;
      font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings:
    "slnt" 0;
    color:rgb(255, 255, 255);
  background-image: url(./earth.jpg);
    background-position: center;
    background-size:cover;
    background-repeat: no-repeat;

/* background-color: hsl(210, 80%, 50%);
    background: rgb(174,200,238);
background: linear-gradient(90deg, rgba(174,200,238,1) 0%, rgba(25,113,216,1) 100%);
    color: rgb(0, 0, 0);
   */ 
}



.inter-fontinter {
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  font-weight: 750;
  font-style: normal;
  font-variation-settings:
    "slnt" 0;
}
.container {
    margin: 0 auto;
}
img{
    width: 20%;
    height: auto;
}

.header {
    padding: 10px 0;
    text-align: center;
    color:#ffffff;
        background: rgba(2, 35, 171, 0.5);
}

.header h1 {
    margin: 0;
    font-size: 2.5rem;
}

.main-content {
    padding: 30px 0;
    background: rgba(2, 35, 171, 0.5);
}

.section {
    margin-bottom: 40px;
    max-width: 1200px;
        padding: 20px;
}

.section h2 {
    color: #ffffff; /* Yellow */
}

.section p {
    line-height: 1.5;
  background-color:black;
  background: rgba(0, 0, 0, 0.5);
border-radius: 10px;
padding: 10px;


}

.footer {
    color: white;
    text-align: center;
    padding: 20px 0;
}
